const STATIC_PATH = "/static";
const EXT_PNG = "png";
const IMAGE_SCALE_1X = "@1x";
const IMAGE_SCALE_2X = "@2x";
const IMAGE_SCALE_3X = "@3x";

function friendlyDate(timestamp) {
	var formats = {
		'year': '%n% ',
		'month': '%n% ',
		'day': '%n% ',
		'hour': '%n% ',
		'minute': '%n% ',
		'second': '%n% ',
	};

	var now = Date.now();
	var seconds = Math.floor((now - timestamp) / 1000);
	var minutes = Math.floor(seconds / 60);
	var hours = Math.floor(minutes / 60);
	var days = Math.floor(hours / 24);
	var months = Math.floor(days / 30);
	var years = Math.floor(months / 12);

	var diffType = '';
	var diffValue = 0;
	if (years > 0) {
		diffType = 'year';
		diffValue = years;
	} else {
		if (months > 0) {
			diffType = 'month';
			diffValue = months;
		} else {
			if (days > 0) {
				diffType = 'day';
				diffValue = days;
			} else {
				if (hours > 0) {
					diffType = 'hour';
					diffValue = hours;
				} else {
					if (minutes > 0) {
						diffType = 'minute';
						diffValue = minutes;
					} else {
						diffType = 'second';
						diffValue = seconds === 0 ? (seconds = 1) : seconds;
					}
				}
			}
		}
	}
	return formats[diffType].replace('%n%', diffValue);
}

function getStaticResource(name, ext) {
	return `${STATIC_PATH}/${name}${IMAGE_SCALE_3X}.${ext || EXT_PNG}`;
}

const objectMap = (obj, fn) =>
	Object.fromEntries(
		Object.entries(obj).map(
			([k, v], i) => [k, fn(v, k, i)]
		)
	)
  

export {
	friendlyDate,
	getStaticResource,
	objectMap
}
